<!-- -->
<template>
	<el-row>
    	<el-col :span="24">
	    	<el-row>
	    		<el-col :span="24">
					<p></p>
				    <div class="nav">
	    				<div class="navs">
	    					<label></label>
	    					<el-input placeholder="" style="width: 340px"  v-model="queryCriteria.type">
							    <el-select slot="prepend" placeholder="" style="width: 110px" v-model="queryCriteria.typeValue">
							        <el-option label="ID" value="ID"></el-option>
							        <el-option label="" value=""></el-option>
							        <el-option label="" value=""></el-option>
							        <el-option label="" value=""></el-option>
							        <el-option label="" value=""></el-option>
							        <el-option label="" value=""></el-option>
							    </el-select>
						    </el-input>
	    				</div>
					    <div class="navs">	
					    	<label>/</label>
					    	<el-select v-model="queryCriteria.typeName" slot="prepend" placeholder="" style="width: 220px">
						        <el-option label="" value=""></el-option>
						        <el-option label="" value=""></el-option>
						    </el-select>
					    </div>
					    <div class="navs">
					    	<label>/</label>
					    	<el-select v-model="queryCriteria.sharedState " slot="prepend" placeholder="" style="width: 220px">
						        <el-option label="" value=""></el-option>
						        <el-option label="" value=""></el-option>
						    </el-select>
					    </div>
					    <div class="navs">
					    	<label>APP</label>
					    	<el-select v-model="queryCriteria.grade" slot="prepend" placeholder="" style="width: 220px">
						        <el-option label="" value=""></el-option>
						        <el-option label="" value=""></el-option>
						    </el-select>
					    </div>
					    <div class="navs">
					    	<label></label>
					    	<el-select v-model="queryCriteria.platform" slot="prepend" placeholder="" style="width: 220px">
						        <el-option label="" value=""></el-option>
						        <el-option label="" value=""></el-option>
						    </el-select>
					    </div>
					    <div class="navs">
					    	<label></label>
					    	<el-select v-model="queryCriteria.nameType" slot="prepend" placeholder="" style="width: 220px">
						        <el-option label="" value=""></el-option>
						        <el-option label="" value=""></el-option>
						    </el-select>
					    </div>
	    			</div>
	    			<div class="nav_s">
				    	<el-button class="button" type="success" @click="QueryCriteria" style="width:140px;font-size:16px" size="small"></el-button>
				    	<el-button type="danger" @click="reset"  size="small" style="width:70px;font-size:16px"></el-button>
					</div>
	    		</el-col>
	    	</el-row>
	    	<el-row>
	    		<el-col :span="24" class="table">
	    			<el-table
						border
						stripe
					    :data="WorksList"
					    style="width: 100%">
					    <el-table-column
					      prop="opusId"
					      label="ID"
					      width="auto">
					    </el-table-column>
					    <el-table-column
					      prop="title"
					      label=""
					      width="auto">
					    </el-table-column>
					    <el-table-column
					      prop="userName"
					      label=""
					      width="auto">
					    </el-table-column>
					     <el-table-column
					      prop="releaseTime"
					      label=""
					      width="auto">
					    </el-table-column>
					     <el-table-column
					      prop="type"
					      label="/"
					      >
					    </el-table-column>
					     <el-table-column
					      prop="tagst"
					      label=""
					     >
					    </el-table-column>
					     <el-table-column
					      prop="type"
					      label=""
					     >
					    </el-table-column>
					    <el-table-column
					      prop="description"
					      label=""
					      >
					    </el-table-column>
					    <el-table-column
					      prop="audioName"
					      label=""
					     >
					    </el-table-column>
					    <el-table-column
					      prop="state"
					      label="/"
					      >
					    </el-table-column>
					    <el-table-column
					      prop="play"
					      label=""
					      >
					    </el-table-column>
					    <el-table-column
					      prop="store"
					      label=""
					      >
					    </el-table-column>
					    <el-table-column
					      prop="comment"
					      label=""
					     >
					    </el-table-column>
					    <el-table-column
					      prop="forward"
					      label=""
					     >
					    </el-table-column>
					    <el-table-column
					      prop="videoAppVersion"
					      label="APP"
					     >
					    </el-table-column>
					    <el-table-column
					      prop="videoAppVersion"
					      label=""
					      >
					    </el-table-column>
					    <el-table-column
					      prop="dataState"
					      label=""
					     >
					    </el-table-column>
					    <el-table-column label="" width="100">
					        <template scope="scope">
						        <el-button
						          size="small"
						          @click="AddSelecteS(scope.row)"></el-button>
					        </template>
					    </el-table-column>
					</el-table>
    			</el-col>
	    	</el-row> 		
    		<el-row>
		    	<el-col :span="24" class="table">
		    		<el-pagination
				        @current-change="handleCurrentChange"
				        :current-page="currentPage"
				        :page-size="pageSize"
				        layout="prev, pager, next, jumper"
				        :total="totalElements">
				    </el-pagination>
		    	</el-col>
    		</el-row>
    	</el-col>
    </el-row>
</template>
<script>
import {mapMutations,mapActions, mapState} from "vuex"
import {WORKSLIST,DELETESELECTED} from "../../../../store/worksManagement/Selected.js"
	export default{
		name:"worksSelected",
		created(){
			this.WORKSLIST()
		},
		// mounted(){
		// 	this.WorksList.forEach((){
		// 		// statements
		// 	});
		// },
		data(){
			return{
				//
				queryCriteria:{
					type:"",
					typeValue:"",
					typeName:"",
					sharedState:"",
					grade:"",
					platform:"",
					nameType:""
				},
				//
				currentPage:1,
			}
		},
		computed:{
	    	...mapState("Selected",["pageSize","totalElements","WorksList"]),
	    },
		methods:{
	      	...mapActions("Selected",["WORKSLIST","DELETESELECTED"]),
			...mapMutations("Selected",["getPageSize"]),
			...mapMutations("createSpecial",["PuxhWorksList","dialogVisiblefalse"]),
        	handleCurrentChange(val) {
        		this.getPageSize(val)
        		this.WORKSLIST()
        	},
        	QueryCriteria(){
        		console.log(this.queryCriteria)
        	}, 
        	reset(){
        		let qreryCriterias={
					type:"",
					typeValue:"",
					typeName:"",
					sharedState:"",
				}
				this.queryCriteria = qreryCriterias
        	},
        	AddSelecteS(row){
        		this.dialogVisiblefalse()
        		this.PuxhWorksList(row)
        	}
		}
	}
</script>
<style scoped>
	.type{
		display: inline-block;
	}
	.type > label{
		padding-left: 20px;
	}
	.button{
		margin-left: 20px;
	}
	.table{
		padding-top: 20px;
		display: flex;
		flex-wrap: wrap;
		justify-content: flex-end;
	}
	.label{
		margin-top: 20px
	}
	.nav{
		display: flex;
		flex-wrap: wrap;
	}
	.navs{
		display: flex;
		flex-direction: column;
		margin-right: 20px;
		margin-top: 20px;
	}
	.navs label{
		font-size: 16px;
		margin-bottom: 15px;
	}
	.nav_s{
		display: flex;
		justify-content: flex-end; 
		margin-right: 30px;
		margin-top: 20px;
	}
	.nav_s button{
		margin-left: 20px;
	}
</style>


